.body {
  display: flex;
  margin-bottom: 100px;
  background-color: #fff;
  padding: 20px;
  border-radius: 20px;
  max-width: 1000px;
}

.img{
  max-width: 150px;
  width: 100%;
  height: 100%;
  position: relative;
  top: -50px;
  left: -50px;
  box-shadow: 10px 10px 10px rgb(187, 187, 187);
}
.genres{
  padding: 0;
  display: flex;
}
.genres li {
  padding-right: 10px;
}
.title a{
  text-decoration: none;
  color: rgb(243, 191, 2);
  font-weight: bold;
  font-size: 30px;
}